






























import {
  Component,
  Vue,
  Prop
} from 'vue-property-decorator'
import IndexCoupon from "@/components/couponCom/index-coupon/indexCoupon.vue"
import Auth from "@/components/auth/auth.vue";
import {Animation,HomeCouponState,ApiCouponItem} from "./homeCouponType"
  import {
  getCouponIndex,
  getOneCoupon,
  getAllCoupon,
} from "@/api/modules/coupon";
import {
  loginGetInt
} from "@/api/modules/integral";

@Component({
  components: {
    IndexCoupon,
    Auth
  }
})
export default class HomeCoupon extends Vue implements HomeCouponState {
  @Prop({
    type: Boolean,
    default: false
  })
  couponType: Boolean
  @Prop() animation:Animation

  couponList: Array<ApiCouponItem>= []
  isShow = false
  authType = false
  canGet = true
  timeout=0

  // 组件周期函数--监听组件挂载完毕
  mounted() {
    this.getCouponList();
    this.isShow = false
    this.$emit("stopRoll", false);
    const nowDay = Math.floor((+new Date())/(1000*60*60*24))
    const storageDay = uni.getStorageSync('couponDay')
    if(!storageDay || nowDay > storageDay){
      this.appLaunchInit();
    }
  }

  async appLaunchInit() {
    await this.getCouponList();
    let userInfo = this.$STORE.userStore.userInfo
    userInfo = {
      ...userInfo,
      couponType: false
    }
    this.$STORE.userStore.setUserInfo(userInfo)
    const nowDay = Math.floor((+new Date())/(1000*60*60*24))
    if (this.couponList.length > 0) {
      this.isShow = true
      uni.setStorageSync('couponDay', nowDay)
      this.$emit("stopRoll", true);
    }
  }

  authClick(){
    this.setData({
        authType: false,
    });
  }

  /**
   * 获取首页优惠券
   */
  async getCouponList() {
    const shopMsg = this.$STORE.shopSetStore.shopInfo;
    if (!shopMsg || shopMsg.status !== 2) return;

    const res = await getCouponIndex({})
    this.juageUserLogin();
    if (!res.length) {
      this.$emit("stopRoll", false);
      return;
    }
    res.forEach((item, index) => {
      item.hasUse = false;
      item.index = index;
      item.restNum =
        item.available > item.totalAvailable ?
        item.totalAvailable :
        item.available;
    });
    this.couponList = res
    this.canGet = true
  }

  /**
   * 判断用户登录能否在获得积分
   */
  async juageUserLogin() {
    const userInfo = this.$STORE.userStore.userInfo;
    if (!userInfo.whetherAuthorization) return;
    if (!this.$STORE.setStore.integral.load) return;
    const res = await loginGetInt({});
    if (res) {
      uni.showToast({
        title: `登录成功,获得${res}积分`,
        icon: "none"
      });
    }
  }

  /**
   * 领取优惠券
   * @param options
   */
  getCoupon(options) {
    const userInfo = this.$STORE.userStore.userInfo;
    if (!userInfo.whetherAuthorization) {
      this.authType = true
      this.isShow = false
      this.$emit("stopRoll", false);
      return;
    }
    const currentItem = this.couponList[options.index];
    getOneCoupon(options.id, {})
      .then(() => {
        currentItem.restNum = currentItem.restNum - 1;
        if (currentItem.restNum === 0) {
          currentItem.hasUse = !currentItem.hasUse;
        }
        this.couponList[options.index] = currentItem
        uni.showToast({
          title: "领取成功",
          icon: "none",
          duration: 500
        });
      })
      .catch(() => {
        uni.showToast({
          title: "优惠券已被领光",
          icon: "none",
          duration: 1000
        });
        currentItem.hasUse = !currentItem.hasUse;
        this.couponList[options.detail.index] = currentItem
      });
  }

  /**
   * 一键获取所有
   */
  getAll() {
    const userInfo = this.$STORE.userStore.userInfo;
    if (!userInfo.whetherAuthorization) {
      this.authType = true
      this.isShow = false
      this.$emit("stopRoll", false);
      return;
    }
    if (this.timeout) {
      clearTimeout(this.timeout);
    }
    this.timeout = setTimeout(() => {
      const ids = [] as any;
      this.couponList.forEach((item: any) => {
        item.hasUse = true;
        ids.push(item.id);
      });
      getAllCoupon(ids, {}).then(() => {
        uni.showToast({
          title: "领取成功,可在我的优惠券查看",
          icon: "none"
        });
        this.canGet = false
      });
    }, 300);
  }

  cancel() {
    const type = this.couponList.every((item: any) => {
      return item.hasUse === true;
    });
    this.$emit("cancel", type ? 1 : 0);
    // this.store.settingStore.tabVisible = true;
    let userInfo = this.$STORE.userStore.userInfo
    userInfo = {
      ...userInfo,
      couponType: true
    }
    this.isShow = false;
    this.$STORE.userStore.setUserInfo(userInfo)
    this.$emit("stopRoll", false);
  }

  /**
   * 显示弹窗
   */
  async couponPop() {
    await this.getCouponList();
    // this.store.settingStore.tabVisible = false;
      this.setData({
        isShow: true
      })
      this.$emit("stopRoll", true);
    

    const userInfo = {
      ...this.$STORE.userStore.userInfo,
      couponType: true
    }
    this.$STORE.userStore.setUserInfo(userInfo)
  }

  /**
   * 隐藏优惠券
   */
  hideCoupon() {
    this.isShow = false
  }
}
